import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TourPackagesComponent } from './tour-packages/tour-packages.component';
import { SharedModule } from '../shared/shared.module';
import { SummaryInfoComponent } from './summary-info/summary-info.component';
import { DetailsPageComponent } from './details-page/details-page.component';
import { EditPackageComponent } from './edit-package/edit-package.component';

const routes:Routes = [
  { path: 'tour-packages/:name', component: TourPackagesComponent },
  { path: 'summeryView', component: SummaryInfoComponent },
  { path: 'details', component: DetailsPageComponent },
  { path: 'product/:id', component: DetailsPageComponent },
  { path: 'product-modification/:id', component: EditPackageComponent }, 
]

@NgModule({
  declarations: [
    TourPackagesComponent,
    SummaryInfoComponent,
    DetailsPageComponent,
    EditPackageComponent
  ],
  imports: [
    RouterModule.forChild(routes),
    SharedModule
  ],
  exports: [
    RouterModule,
    SummaryInfoComponent
  ] 
})
export class ProductModule { }
