import { Component, OnInit } from '@angular/core';
import { SummaryInfoService } from 'src/app/product/summary-info/summary-info.service';
import { ActionInfoService } from '../inactive-info/action-info.service';


@Component({
  selector: 'app-allpackage-info',
  templateUrl: './allpackage-info.component.html',
  styleUrls: ['./allpackage-info.component.css']
})
export class AllpackageInfoComponent implements OnInit {

  response;
  productArr;
  loading =true;
  errorMessage;
  agencyId;
  agencyName;
  activeArr = [];
  deletedArr = [];
  inActiveArr =[];
  mapSelectedValue = new Map();
  isSuccessMessage = false;
  isErrorMessage = false;
  successMsg;
  errorMsg;
  activeBLoading = false;
  deleteBLoading = false;
  inActiveBLoading = false;
  constructor(private summary : SummaryInfoService, private actionInfoService: ActionInfoService) {

    let currentUser = localStorage.getItem('currentUser');
    try {
      var obj = JSON.parse(currentUser);
      if (obj) {
        this.agencyId = obj.agencyId;
        this.agencyName = obj.agencyName;
      }
    } catch (error) {
      console.log(error)
    }
  }


  // getAllActionDetails
  ngOnInit() {
    // this.summary.getProducts().subscribe({
    //   next: products => {
    //     this.response = products;
    //     this.productArr = this.response.packageSummeryRS;
    //     this.loading = false;
    //     // this.filteredProducts = this.products;
    //   },
    //   error: err => this.errorMessage = err
    // });

    let Req = {
      agencyId: this.agencyId,
      agencyName: this.agencyName
    }
    this.actionInfoService.getAllActionDetails(Req).subscribe({
      next: products => {
        this.response = products;
        this.productArr = this.response.packageSummeryRS;
        this.loading = false;
        // this.filteredProducts = this.products;
      },
      error: err => this.errorMessage = err
    });

  }



  
  onChangeActiveValue(value, packageId) {
    this.mapSelectedValue.set(packageId, 'Active');
    console.log("onChangeActiveValue------" + value + " packageId----" + packageId);
    // this.getMapValue();
  }

  onChangeDeleteValue(value, packageId) {
    this.mapSelectedValue.set(packageId, 'Delete');
    console.log("onChangeDeleteValue------" + value + " packageId----" + packageId);
    // this.getMapValue();
  }

  onChangeInActiveValue(value, packageId) {
    this.mapSelectedValue.set(packageId, 'InActive');
    console.log("onChangeInActiveValue------" + value + " packageId----" + packageId);
    // this.getMapValue();
  }

  // getMapValue() {
  //   for (let key of this.mapSelectedValue.keys()) {
  //     let value = this.mapSelectedValue.get(key);
  //     console.log("key------" + key + " value----" + value);

  //   }
  // }

/**
 * Active part
 */

  getActiveList() {
    this.activeArr = [];
    for (let key of this.mapSelectedValue.keys()) {
      let value = this.mapSelectedValue.get(key);
      if ('Active' == value) {
        this.activeArr.push(key);
      }
    }
    return this.activeArr;
  }

  updateActiveList() {
    let activeList = this.getActiveList();
    this.activeBLoading = true;
    let Req = {
      agencyId: this.agencyId,
      agencyName: this.agencyName,
      activePackage: activeList
    }

    if (activeList.length > 0) {
      this.actionInfoService.modifyCompleateDetails(Req).subscribe({
        next: products => {
          this.response = products;
          this.activeBLoading = false;
          this.successFullMessage();
        },
        error: err => this.errorMessage = err
      });
    } else {
      this.activeBLoading = false;
      this.errorFullMessage(true);
    }
  }


/**
 * Delete part
 */

  getDeleteList() {
    this.deletedArr = [];
    for (let key of this.mapSelectedValue.keys()) {
      let value = this.mapSelectedValue.get(key);
      if ('Delete' == value) {
        this.deletedArr.push(key);
      }
    }
    return this.deletedArr;
  }

  updateDeleteList() {
    let deleteList = this.getDeleteList();
    this.deleteBLoading = true;
    let Req = {
      agencyId: this.agencyId,
      agencyName: this.agencyName,
      deletedPackage: deleteList
    }

    if (deleteList.length > 0) {
      this.actionInfoService.modifyCompleateDetails(Req).subscribe({
        next: products => {
          this.response = products;
          this.deleteBLoading = false;
          this.successFullMessage();
        },
        error: err => {
          this.errorMessage = err
          this.errorFullMessage(false);
        }
      });
    } else {
      this.deleteBLoading = false;
      this.errorFullMessage(true);
    }

  }

/**
 * In Active part
 */
  getInActiveList() {
    this.inActiveArr = [];
    for (let key of this.mapSelectedValue.keys()) {
      let value = this.mapSelectedValue.get(key);
      if ('InActive' == value) {
        this.inActiveArr.push(key);
      }
    }
    return this.inActiveArr;
  }

  updateInActiveList(){
    let inActiveList = this.getInActiveList();
    this.deleteBLoading = true;
    let Req = {
      agencyId: this.agencyId,
      agencyName: this.agencyName,
      inActivePackage: inActiveList
    }

    if (inActiveList.length > 0) {
      this.actionInfoService.modifyCompleateDetails(Req).subscribe({
        next: products => {
          this.response = products;
          this.deleteBLoading = false;
          this.successFullMessage();
        },
        error: err => {
          this.errorMessage = err
          this.errorFullMessage(false);
        }
      });
    } else {
      this.deleteBLoading = false;
      this.errorFullMessage(true);
    }

  }


  successFullMessage() {
    this.successMsg = 'Successfuly updated .....'
    this.isSuccessMessage = true;
    let data = this;
    setTimeout(function () {
      data.isSuccessMessage = false;
    }, 2000)
  }

  errorFullMessage(isPlaneErr: boolean) {
    if (isPlaneErr) {
      this.errorMsg = 'Before click any Action please select some item ....'
    } else {
      this.errorMsg = 'Unable to Update (Server Error) ......'
    }
    this.isErrorMessage = true;
    let data = this;
    setTimeout(function () {
      data.isErrorMessage = false;
    }, 3000)
  }



}
