import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { AppConstants } from 'src/app/common/AppConstants';
import { Observable, throwError } from 'rxjs';
import { IProductDetails } from 'src/app/product/details-page/details-api-rs';
import { tap, catchError } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class AdminInfoService {

  _baseURL: string;
  constructor(private http: HttpClient) {
    this._baseURL = AppConstants.apiURL;
  }



  public updateProducts(requestData: any): Observable<IProductDetails> {
    let url = this._baseURL + 'SearchDetails/updateProductInfo';
    return this.http.post<IProductDetails>(url, requestData)
      .pipe(tap(data => { console.log('All: ' + JSON.stringify(data)) }
      ),
        catchError(this.handleError)
      );
  }


  public getProductsInfo(agencyId, agencyName): Observable<PackageCountRS> { 
    let url = this._baseURL + 'ActionService/allPackageOverview';
    let req = { 
        agencyId :  agencyId,
        agencyName : agencyName
    }
    return this.http.post<PackageCountRS>(url, req)
      .pipe(tap(data => { console.log('All: ' + JSON.stringify(data)) }
      ),
        catchError(this.handleError)
      );
  }  


  getProductInfoDetails(request) : Observable<any> {
    let url = this._baseURL + 'ActionService/packageInfoDetails';
    return this.http.post<any>(url, request)
      .pipe(tap(data => { console.log('All: ' + JSON.stringify(data)) }
      ),
        catchError(this.handleError)
      );
  }


  public uploadImageFile(request: any): Observable<any> { 
    let url = this._baseURL + 'ImageService/imageUpload';
    return this.http.post<any>(url, request)
      .pipe(tap(data => { console.log('All: ' + JSON.stringify(data)) }
      ),
        catchError(this.handleError)
      );
  }  


  private handleError(err: HttpErrorResponse) {
    // in a real world app, we may send the server to some remote logging infrastructure
    // instead of just logging it to the console
    let errorMessage = '';
    if (err.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      errorMessage = `An error occurred: ${err.error.message}`;
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      errorMessage = `Server returned code: ${err.status}, error message is: ${err.message}`;
    }
    console.error(errorMessage);
    return throwError(errorMessage);
  }


}



export interface PackageCountRS{
  packageList : Array<PackageCount>;

}
export interface PackageCount{
  name : string;
  count : number;
  isActionRequire : boolean,
  actionUrl : string
}