import { Component, OnInit, Input, OnChanges, Injectable } from '@angular/core';
import { IProductInfo } from 'src/app/product/summary-info/product';
import { SummaryInfoService } from 'src/app/product/summary-info/summary-info.service';


@Component({
  selector: 'app-summery-view',
  templateUrl: './summery-view.component.html',
  styleUrls: ['./summery-view.component.css']
})

export class SummeryViewComponent implements OnInit , OnChanges{

  // @Input() productArrInfo : IProductInfo[];
  @Input() productArr : IProductInfo[];
  loading =true;
  @Input() isButtonEnabled : boolean;
  isModalOpen :boolean = false;
  isModalLoading :boolean =true;
  errorMessage;
  bookerInfo;
  // productArr =[];
  constructor(private summaryInfoService :SummaryInfoService) { 
    this.isButtonEnabled = true;
  }
  ngOnChanges(changes: import("@angular/core").SimpleChanges): void {
    // this.productArr =this.productArrInfo;
    console.log(this.productArr)
    if(this.productArr){
      this.loading = false;
    }
  }

  ngOnInit() {
    
  }

  openModalInfo(agencyId){
    // console.log('inside--openModalInfo---------------------')
    this.isModalOpen = true;
    let req= {
      agencyId : agencyId,
      agencyName : ''
    };
    this.summaryInfoService.getBookerDetails(req).subscribe({
      next: products => {
        this.bookerInfo = products;
        // console.log('productResp'+productResp
        this.isModalLoading = false;
      },
      error: err =>{ this.errorMessage = err  }
    });

  }


  closeModalInfo(){
    // console.log('inside--openModalInfo---------------------')
    this.isModalOpen = false;
  }


}
