import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../Auth/authentication.service';

@Component({
  selector: 'app-sign-up-info',
  templateUrl: './sign-up-info.component.html',
  styleUrls: ['./sign-up-info.component.css']
})
export class SignUpInfoComponent implements OnInit {

  fName;
  lName;
  email
  phoneNo
  dateOfBirth
  password
  confirmPassword
  userRole = 'PlaneUser';
  required = true;
  isLoading: boolean = false;
  currency;
  agencyName;
  isSuccessMessage = false;
  isErrorMessage = false;
  successMsg;
  errorMsg;
  isAgencyUser: boolean = false;
  isAccept :boolean =false;
  PlaneUser ='PlaneUser';
  TravelAgencyUser ='TravelAgencyUser';
  constructor(private authenticationService: AuthenticationService) { }

  ngOnInit() {
  }

  onChangeValue(isSelect, value) {
    console.log('isSelect ' + isSelect + ' value ' + value)
    if ('trvelAgent' == value) {
      this.userRole = 'AgencyUser';
      this.isAgencyUser = true;
    } else {
      this.userRole = 'PlaneUser';
      this.isAgencyUser = false;
    }
  }

  userSignUp() {
    this.isLoading = true;
    let allOk: boolean = true;
    console.log('inside....'+this.isAccept)

    if(!this.email){
      this.errorFullMessage('Email required..');
      allOk = false;
      return;
    }
    if (this.password) {
      if (this.password != this.confirmPassword) {
        this.errorFullMessage('Password mish match try again..')
        this.password = '';
        this.confirmPassword = '';
        allOk = false;
        return;
      }
    } else {
      this.errorFullMessage('Password required..');
      allOk = false;
      return;
    }

    if(!this.isAccept){
      this.errorFullMessage('Acccept Terms & Policy ....');
      allOk = false;
      return;
    }

    if (allOk) {
      let request = {
        userName: this.email,
        password: this.password,
        phoneNo: this.phoneNo,
        firstName: this.fName,
        lastName: this.lName,
        userRole: this.userRole,
        mailId: this.email,
        currency: this.currency,
        agencyName: this.agencyName
      }
      console.log("request" + JSON.stringify(request));

      this.authenticationService.userRegistration(request).subscribe({
        next: products => {
          let response = products;
          let isValid = response.isValidUser;
          console.log('isValid--'+isValid)
          if (isValid == 'true') {
            this.successFullMessage();
          } else {
            let er =response.error;
            if(er){
              this.errorFullMessage(er);
            }else{
              this.errorFullMessage('Unable to create user Please provide currect info ....');
            }
          }
          this.isLoading = false
        },
        error: err => {
          this.isLoading = false;
          this.errorFullMessage('Unable to create user ......');
        }
      });
    }else{
      this.isLoading = false;
    }

  }



  successFullMessage() {
    this.successMsg = 'User Registration Successfuly Done, Please Login .....'
    this.isSuccessMessage = true;
    let data = this;
    setTimeout(function () {
      data.isSuccessMessage = false;
    }, 3000)
  }

  errorFullMessage(message: string) {
    this.errorMsg = message
    this.isErrorMessage = true;
    let data = this;
    setTimeout(function () {
      data.isErrorMessage = false;
    }, 3000)
    this.isLoading = false;
  }


}
