import { Component, OnInit } from '@angular/core';
import { IProduct, IProductInfo } from './product';
import { SummaryInfoService } from './summary-info.service';

@Component({
  selector: 'app-summary-info',
  templateUrl: './summary-info.component.html',
  styleUrls: ['./summary-info.component.css']
})
export class SummaryInfoComponent implements OnInit {

  response : IProduct;
  productArr : IProductInfo [] = [];
  errorMessage ;
  loading : boolean = true;
  buttonLoader : boolean = false;
  destinationsName ='';
  private _destinationsName1 :string;
  public get destinationsName1() {
    return this._destinationsName1; 
  }
  public set destinationsName1(value) {
    this._destinationsName1 = value;
  } 
  persionMessage : string ='Per person on twin sharing basis';
  constructor(private summary : SummaryInfoService ) {  
    // app.setIsLeftPanalRequired(true);
  }

  title;

  updateTitle(title: string) {
    this.title.setTitle(title);
  }

  ngOnInit() { 
    // console.log("-------------------"+this.summary.getHardCodedProducts())
    // setTimeout(function(){
    try {
      let isFirstTime = localStorage.getItem('isAlreay');
      // console.log('isFirstTime--'+isFirstTime);
      if(!isFirstTime){
        // console.log('inside...........')
        this.summary.getDummyProducts().subscribe({
          next: products => {
            this.response = products;
            this.productArr = this.response.packageSummeryRS;
            this.loading = false;
            // this.filteredProducts = this.products;
          },
          error: err => this.errorMessage = err
        });
        localStorage.setItem('isAlreay' , 'true');
    }

    } catch (error) {
      
    }


    this.summary.getProducts().subscribe({
      next: products => {
        this.response = products;
        this.productArr = this.response.packageSummeryRS;
        this.loading = false;
        // this.filteredProducts = this.products;
      },
      error: err => this.errorMessage = err
    });

    // }, 3000);
    // this.response = this.summary.getHardCodedProducts();
    // this.productArr = this.response.packageSummeryRS;
  }


  productResponse ;
  getSummeryData(city, date){
    this.loading = true;
    this.buttonLoader = true;
    console.log("getSummeryData########city#########--"+ city );
    console.log('-----------date*************'+date);
    console.log('-----------destinationsName1*************'+ this.destinationsName1);
    this.productResponse = this.productArr;
    this.summary.getSearchProducts(city, date).subscribe({
      next: products => {
        this.response = products;
        this.productArr = this.response.packageSummeryRS;
        this.loading = false;
        this.buttonLoader = false;
        
        // let productValue = this.response.packageSummeryRS;
        // let value = [];
        // if(city != undefined || city !=''){
        //   value = this.performfilter(city);
        //   console.log(value);
        // }
        // this.productArr = value.length > 0 ? value : productValue;
        

      },
      error: err => this.errorMessage = err
    });
  }

  performfilter(filterBy: string): IProductInfo[] {
    filterBy = filterBy.toLowerCase();
    return this.productResponse.filter((product: IProductInfo) =>
      product.packageName.toLocaleLowerCase().indexOf(filterBy) != -1
      || product.tripPlaces.toString().toLocaleLowerCase().indexOf(filterBy) != -1
    );
  }

  
  
}
