import { Component, OnInit, Injectable } from '@angular/core';
import { LoginInfoComponent } from '../login-info/login-info.component';
import { AuthenticationService } from '../Auth/authentication.service';
import { Observable, Subscription } from 'rxjs';
import { Router } from '@angular/router';
import { LogoutInfoComponent } from '../login-info/logout-info.component';

@Component({
  selector: 'app-heder-info',
  templateUrl: './heder-info.component.html',
  styleUrls: ['./heder-info.component.css']
})
@Injectable({ providedIn: 'root' })
export class HederInfoComponent implements OnInit {

  isProfileDataShoW : boolean = false;
  isAlreadyLogIn : boolean = false;

  isLoggedIn$: Observable<boolean>;


  userName = 'Profile';
  // { "userId": "101", "userName": "Anup Ghosh", "token": "665546", "firstName": "Anup", "lastName": "Ghosh", "userType": "Admin", "userRole": "Admin", "mailId": "anupghosh9635@gmail.com", "isValidUser": "true", "agencyName": "Ghosh Agency" }
  constructor(private authService : AuthenticationService, private loginService : LoginInfoComponent, private router : Router, private logoutInfoComponent : LogoutInfoComponent) { 
    
        let currentUser = localStorage.getItem('currentUser');
        if(currentUser){
          this.isProfileDataShoW = true;
        }

        try {
          let obj = JSON.parse(currentUser);
          if (obj) {
            this.userName = obj.userName;
            this.isAlreadyLogIn = true;
          }
        } catch (error) {
          
        }

        this.subscription = this.logoutInfoComponent.getName().subscribe(
          (message) => {
            console.log('subscription----------------------')
            this.userName = message;
          }
        );
        
        // else{
        //   this.router.navigate(['login']);
        // }

    // if(loginService.getLoggedInName){
    //   this.changeName(true);
    // }
    
    // this.isLoggedIn$ = loginService.isLoggedIn;
    // loginService.loggedInNameDay().subscribe(name => {console.log("----------------loggedInName----"+name)});

  }
  
  subscription: Subscription;

  ngOnInit() {
    // this.loginService.isLoggedIn.subscribe( name =>{ this.changeName(name); console.log("----------------name----"+name) });
    // this.loginService.loggedInNameDay.subscribe(name => {console.log("----------------loggedInName----"+name)});
    this.authService.getLoggedInName.subscribe(name =>{ this.changeName(name) ; console.log('getLoggedInName subscribe*************'+name)});
    
  }
  

  private changeName(name: any): void {
    if(name){
      console.log('inside-------------------changeName ')
      this.isProfileDataShoW = true;
      this.userName =name;
      this.isAlreadyLogIn = true;
    }
  }



  logOutUser(){
    localStorage.clear();
    sessionStorage.clear();
    this.isProfileDataShoW = false;
    this.userName ='Profile';
    this.isAlreadyLogIn = false;
    this.router.navigate(['home']); 
  }

  
}
