import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AuthenticationService } from '../Auth/authentication.service';
import { AdminInfoService } from '../action/admin-info/admin-info.service';

@Component({
  selector: 'app-profile-info',
  templateUrl: './profile-info.component.html',
  styleUrls: ['./profile-info.component.css']
})
export class ProfileInfoComponent implements OnInit {

  userName = 'Profile';
  agencyId;
  profile : Profile;
  loading = true;
  errorMessage;
  isSummeryImageLoading = false;
  
  constructor(private router : Router, private  authenticationService: AuthenticationService,private adminInfoService : AdminInfoService) { 
    let currentUser = localStorage.getItem('currentUser');
    let obj = JSON.parse(currentUser);
    if (obj) {
      this.userName = obj.userName;
      if (obj.agencyId) {
        this.agencyId = obj.agencyId;
      }
    }else{
      this.router.navigate(['login']);
    }
  }

  ngOnInit() {
    let Req = {
      userName: this.userName,
      agencyId: this.agencyId
    }
    this.authenticationService.profileIinfo(Req).subscribe({
      next: products => {
        this.profile = products;
        if(products.profileUrl){
          this.imagePath = products.profileUrl;
        }
        this.loading = false;
      },
      error: err => this.errorMessage = err
    });
  }

  firstName :string = 'Anup'
  lastName :string = 'Ghosh'
  imagePath : any ='assets/images/package2.jpg' 

  // imagePath: any = 'assets/images/icon/uploadImage.jpg';
  // onSelectFile(event) { // called each time file input changes
  //   console.log(event);
  //   if (event.target.files && event.target.files[0]) {
  //     var reader = new FileReader();
  //     this.imagePath = event.target.files;
  //     console.log('imagePath' + this.imagePath);
  //     reader.readAsDataURL(event.target.files[0]); // read file as data url
  //     reader.onload = (event) => { // called once readAsDataURL is completed
  //       this.imagePath = reader.result; //add source to image
  //       // console.log('url---'+this.urlData)
  //     }
  //   }
  // }



  // imageURL: string;
  onSelectFile(event) { // called each time file input changes
    // console.log(event); 
    // console.log(JSON.stringify(event));
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      // this.imagePath = event.target.files;
      // console.log('imagePath' + this.imagePath);
      reader.readAsDataURL(event.target.files[0]); // read file as data url
      let fileNameUrl =event.target.files[0].name;
      reader.onload = (event) => { // called once readAsDataURL is completed
        console.log(event)
        this.imagePath = reader.result; //add source to image
        // this.uploadImageData(fileNameUrl, this.imagePath);
      }
    }
  }

  uploadImageData(fileName, encodedValue){
    this.isSummeryImageLoading = true;
    let req = {
      "agencyId": this.profile.userId,
      "agencyName": this.profile.agencyName,
      "imageList": [
        {
          "fileName": fileName,
          "imageEncodedInfo": encodedValue
        }
      ]
    };

    this.adminInfoService.uploadImageFile(req).subscribe({
      next: products => {
        let productResp = products.imageList;
        // console.log('productResp'+productResp)
        if(productResp.length> 0){
          let imageList = productResp[0];
          // console.log(imageList);
          this.imagePath= imageList.imageUrl;
          // this.isSummeryImageLoading = false;
          // this.updateImageDetailsView(this.urlData);
        }
      },
      error: err =>{ this.errorMessage = err
        // this.updateImageDetailsView(encodedValue);
      }
    });

  }


}


export interface Profile{
  userId: string,
  userName: string,
  token: string,
  firstName: string,
  lastName: string,
  userType: string,
  userRole: string,
  mailId: string,
  error: string,
  isValidUser: string,
  agencyId: string,
  agencyName: string,
  currency: string,
  agencyReview: string,
  phoneNo: string,
  profileUrl: string,
  website: string,
  city: string,
  address: string,
  state: string
}