import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { IAmenities, AmenitiesService } from 'src/app/common/AmenitiesInfo';
import { PackageCount, AdminInfoService } from 'src/app/action/admin-info/admin-info.service';
import { DayWiseList } from '../details-page/details-api-rs';

@Component({
  selector: 'app-edit-package',
  templateUrl: './edit-package.component.html',
  styleUrls: ['./edit-package.component.css']
})
export class EditPackageComponent implements OnInit {

  packageId;

  isSuccesFullSave = false;
  isCreatePackage = false;
  public imagePath;
  public agencyName;
  uploadImage: any = 'assets/images/icon/uploadImage.jpg';
  placeName = '';
  headingData = '';
  descriptionData = '';
  noofDay: number = 0;
  tripPlaces = [];
  agencyId = '';
  amenityList: IAmenities[] = [];
  aminitiesCheck = [];
  packageType = [];
  packageTypeSelcted = [];
  agencyReview ;
  currency;
  packageCoutInfo : PackageCount[];
  isSummeryVSaveLoading = false;
  isDetailsVSaveLoading = false;
  isSummeryImageLoading = false;
  isDetaisImageLoading = false;
  ismageLoading : boolean [] = [];
  isManualyFileCahge :boolean = false;
  packageName;
  originalPrice;
  offerPrice;
  noOfDays : number;
  noOfNights;
  errorMessage;
  highlights;
  overview;
  urlData: any = 'assets/images/icon/uploadImage.jpg';
  detailsURLData: any = 'assets/images/icon/uploadImage.jpg';
  fileNameUrl;
  amenities: string[] = [];
  loading :boolean = true; 
  constructor( private route: ActivatedRoute, private router : Router, private adminInfoService : AdminInfoService) { 
    this.amenityList = AmenitiesService.getAllAmenities();
    let currentUser = localStorage.getItem('currentUser');
    try {
      var obj = JSON.parse(currentUser);
      if (obj) {
        this.agencyName = obj.agencyName;
        if (obj.agencyId) {
          this.agencyId = obj.agencyId;
        }
        this.currency = obj.currency;
        this.agencyReview = obj.agencyReview;
      }
    } catch (error) {
      console.log(error)
    }

    this.packageType = AmenitiesService.toureType;
    for (let index = 0; index < this.amenityList.length; index++) {
      this.aminitiesCheck.push(false);
    }

    for (let index = 0; index < this.packageType.length; index++) {
      this.packageTypeSelcted.push(false);
    } 
  }

  ngOnInit() {

    let id : string = this.route.snapshot.paramMap.get('id');
    this.packageId = id;

    let request = {
      agencyId : this.agencyId,
      agencyName : this.agencyName,
      packageId : id
    }
    this.adminInfoService.getProductInfoDetails(request).subscribe({
      next: products => {
        this.loading = false;
        let response = products.packageDetailsRS;
        try {
        
         this.agencyName = response.agencyName
         this.agencyReview = response.agencyReview
         this.packageId = response.packageId
         this.packageName = response.packageName ;
          console.log('response.packageName : '+this.packageName)
         this.originalPrice = response.originalPrice
         this.offerPrice = response.offerPrice
         this.noOfDays = response.noOfDays
         this.noOfNights = response.noOfNights
         if(response.tripPlaces){
          this.tripPlaces = response.tripPlaces;
        }
         if( response.amenities != null){
           this.setAmenitiesList(response.amenities);
         }
         if( response.packageCategory != null){
          this.setPackageCategory(response.packageCategory);
         }
         this.currency = response.currency
         this.setHightLightData(response.highlights);
         this.setoverviewData(response.overview);
         if(response.dayWiseList != null && response.dayWiseList.length >0){
          this.dayWiseList =response.dayWiseList;
         }
         this.urlData = response.images[0]
         if(response.overviewImages != null){
          this.detailsURLData = response.overviewImages[0];
         }else{
          this.detailsURLData = response.images[0];
         }
         
 
        } catch (error) {
          console.log(error);
        }
      },
      error: err =>{ this.errorMessage = err;
        this.loading = false;
      }
    });


  }

  amenitiesMap = new Map();
  setAmenitiesMap(){
    for (let index = 0; index < this.amenityList.length; index++) {
      let element = this.amenityList[index];
      this.amenitiesMap.set(element.name, index);
    }
  }

  setAmenitiesList(amenitiesList){
    this.setAmenitiesMap();
    amenitiesList.forEach(elementStr => {
      let id =this.amenitiesMap.get(elementStr);
      if(id){
        this.aminitiesCheck[id] = true;
      }else{
        for (let index = 0; index < this.amenityList.length; index++) {
          const element = this.amenityList[index];
          if(element.name.toLocaleLowerCase() == elementStr.toLocaleLowerCase() ){
            this.aminitiesCheck[id] = true;
            break;
          }
        }
      }
    });
  }

  // packageType
  packageTypeMap = new Map();
  setpackageTypeMap(){
    for (let index = 0; index < this.packageType.length; index++) {
      let element = this.packageType[index];
      this.packageTypeMap.set(element, index);
    }
  }
  setPackageCategory(packageList) {
    this.setpackageTypeMap();
    packageList.forEach(element => {
      let ind =this.packageTypeMap.get(element);
      if(ind){
        this.packageTypeSelcted[ind] = true;
      } else{
        for (let index = 0; index < this.packageType.length; index++) {
          const elementStr = this.packageType[index];
          if(element.toLocaleLowerCase() == elementStr.toLocaleLowerCase() ){
            this.packageTypeSelcted[index] = true;
            break;
          }
        }
      }
    });
  }


setHightLightData(highlightdata){
  if(highlightdata != null){
    let i =0;
    highlightdata.forEach(element => {
      if(i!=0){
        this.highlights = this.highlights+ '\n' +element;
      }else{
        this.highlights = element;
        i=1;
      }

    });
    // this.highlights = highlightdata.toString();
  }
}
setoverviewData(overviewdata){
  if(overviewdata != null){
    this.overview = overviewdata.toString();
  }
}

  getArrayData(): DayWiseList {
    this.noofDay = this.noofDay + 1;
    return {
      "dayNo": "" + (this.noofDay),
      "place": [],
      "amenities": [],
      "images": [
        this.uploadImage
      ],
      "infoData": []
    }
  }

  // imageURL: string;
  onSelectFile(event) { // called each time file input changes
    // console.log(event); 
    // console.log(JSON.stringify(event));
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      this.imagePath = event.target.files;
      console.log('imagePath' + this.imagePath);
      reader.readAsDataURL(event.target.files[0]); // read file as data url
      this.fileNameUrl =event.target.files[0].name;
      reader.onload = (event) => { // called once readAsDataURL is completed
        console.log(event)
        this.urlData = reader.result; //add source to image
        // console.log('url---'+this.urlData)
        this.uploadImageData(this.fileNameUrl, this.urlData);
      }
    }
  }

  uploadImageData(fileName, encodedValue){
    this.isSummeryImageLoading = true;
    let req = {
      "agencyId": this.agencyId,
      "agencyName": this.agencyName,
      "imageList": [
        {
          "fileName": fileName,
          "imageEncodedInfo": encodedValue
        }
      ]
    };

    this.adminInfoService.uploadImageFile(req).subscribe({
      next: products => {
        let productResp = products.imageList;
        // console.log('productResp'+productResp)
        if(productResp.length> 0){
          let imageList = productResp[0];
          // console.log(imageList);
          this.urlData= imageList.imageUrl;
          this.isSummeryImageLoading = false;
          this.updateImageDetailsView(this.urlData);
        }
      },
      error: err =>{ this.errorMessage = err
        this.updateImageDetailsView(encodedValue);
      }
    });

  }


updateImageDetailsView(data){
  if(! this.isManualyFileCahge){
    this.detailsURLData = data;
  }
}


onSelectDetailsFile(event) { // called each time file input changes
  this.isManualyFileCahge = true;
  // console.log(event); 
  if (event.target.files && event.target.files[0]) {
    var reader = new FileReader();
    this.imagePath = event.target.files;
    console.log('imagePath' + this.imagePath);
    reader.readAsDataURL(event.target.files[0]); // read file as data url
    let fileName =event.target.files[0].name;
    reader.onload = (event) => { // called once readAsDataURL is completed
      console.log(event)
      this.detailsURLData = reader.result; //add source to image
      // console.log('url---'+this.urlData)
      this.uploadDetailsImageData(fileName, this.detailsURLData);
    }
  }
}

uploadDetailsImageData(fileName, encodedValue){
  this.isDetaisImageLoading = true;
  let req = {
    "agencyId": this.agencyId,
    "agencyName": this.agencyName,
    "imageList": [
      {
        "fileName": fileName,
        "imageEncodedInfo": encodedValue
      }
    ]
  };

  this.adminInfoService.uploadImageFile(req).subscribe({
    next: products => {
      let productResp = products.imageList;
      // console.log('productResp'+productResp)
      if(productResp.length> 0){
        let imageList = productResp[0];
        // console.log(imageList);
        this.detailsURLData= imageList.imageUrl;
        this.isDetaisImageLoading = false;
      }
      this.isDetaisImageLoading = false;
    },
    error: err =>{ this.errorMessage = err  }
  });

}

  dayWiseList: DayWiseList[] = [this.getArrayData()];

  add() {
    this.dayWiseList.push(this.getArrayData());
    console.log('dayWiseList------' + JSON.stringify(this.dayWiseList));
  }


  onDaySelectFile(event, index) { // called each time file input changes
    console.log(event);
    if (event.target.files && event.target.files[0]) {
      var reader = new FileReader();
      this.imagePath = event.target.files;
      console.log('imagePath' + this.imagePath);
      reader.readAsDataURL(event.target.files[0]); // read file as data url
      let fileName =event.target.files[0].name;
      reader.onload = (event) => { // called once readAsDataURL is completed
        let data = reader.result; //add source to image

        // this.dayWiseList[index].images.push(data);
        this.dayWiseList[index].images[0] = data; ///tempurary hardcoded for array 1st element

        try {
          this.ismageLoading[index] = true;
          let req = {
            "agencyId": this.agencyId,
            "agencyName": this.agencyName,
            "imageList": [
              {
                "fileName": fileName,
                "imageEncodedInfo": data
              }
            ]
          };
      
          this.adminInfoService.uploadImageFile(req).subscribe({
            next: products => {
              let productResp = products.imageList;
              // console.log('productResp'+productResp)
              if(productResp.length> 0){
                let imageList = productResp[0];
                // console.log(imageList);
                this.dayWiseList[index].images[0] = imageList.imageUrl;
                this.ismageLoading[index] = false;
              }
            },
            error: err => this.errorMessage = err
          });
        } catch (error) {
          
        }


      }
    }
  }



  addCityName(city): void {
    if (city) {
      this.tripPlaces.push(city);
      this.placeName = '';
    }
  }

  placeNameArr = [];
  addPlaceName(place, index): void {
    if (place) {
      this.dayWiseList[index].place.push(place);
      this.placeNameArr[index] = '';
      this.placeName = '';
    }
  }

  headingArr: any[] = [];
  descriptionArr: any[] = [];

  addDetailsInfo(headingData, descriptionData, index): void {
    if (headingData || descriptionData) {
      this.dayWiseList[index].infoData.push({ "heading": headingData, "description": descriptionData });
      this.headingArr[index] = '';
      this.descriptionArr[index] = '';
    }
  }

  
  getAmenitiesList(): string[] {
    this.amenities = [];
    for (let index = 0; index < this.aminitiesCheck.length; index++) {
      const element = this.aminitiesCheck[index];
      if (element == true) {
        this.amenities.push(this.amenityList[index].name);
      }
    }
    return this.amenities;
  }
  packageCategory = [];
  getPackageCategory(): string[] {
    this.packageCategory = [];
    for (let index = 0; index < this.packageTypeSelcted.length; index++) {
      const element = this.packageTypeSelcted[index];
      if (element == true) {
        this.packageCategory.push(this.packageType[index]);
      }
    }
    return this.packageCategory;
  }


  //** save summey view data---- */

  saveSummeryView() {
    this.isSummeryVSaveLoading = true;
    let high = this.highlights.split(/\n/g);
    let request = {
      "agencyId": this.agencyId,
      "agencyName": this.agencyName,
      "agencyReview": this.agencyReview,
      "packageId": this.packageId,
      "packageName": this.packageName,
      "originalPrice": this.originalPrice,
      "offerPrice": this.offerPrice,
      "noOfDays": this.noOfDays,
      "noOfNights": this.noOfNights,
      "tripPlaces": this.tripPlaces,
      "amenities": this.getAmenitiesList(),
      "packageCategory": this.getPackageCategory(),
      "images": [this.urlData],
      "currency": this.currency,
      "overviewImages": [this.detailsURLData],
      "highlights": high,
      "overview": [this.overview],
      "dayWiseList": this.dayWiseList
    };

    console.log("this.highlights***************"+this.highlights);
    console.log("this.highlights.split()************"+ high);
    let requestInfo = {
      "packageDetailsRQ" : request
    }

    console.log('Request----' + JSON.stringify(request));


    this.adminInfoService.updateProducts(requestInfo).subscribe({
      next: products => {
        let productResp = products.packageDetailsRS;
        this.packageId = productResp.packageId;
        this.isSummeryVSaveLoading = false;
        this.isSuccesFullSave = true;
        let data = this;
        setTimeout(function () {
          data.isSuccesFullSave = false;
          console.log('insidesetTimeout****************************************' + data.isSuccesFullSave)
        }, 2000)

      },
      error: err =>{ this.errorMessage = err ; }
    });


    console.log(event);
    // this.isSuccesFullSave = true;
    // let data = this;
    // setTimeout(function () {
    //   data.isSuccesFullSave = false;
    //   console.log('insidesetTimeout****************************************' + data.isSuccesFullSave)
    // }, 2000)
    // console.log('insidesetTimeout*********'+this.isSuccesFullSave)
  }




}
